/* ==================
 *  VARIABLES
 * ================== */
 :root {
  --color-black: hsl(0, 0%, 30%);
  --color-black-light: hsl(0, 0%, 60%);
  --color-gray: hsl(0, 0%, 90%);
  --color-white: hsl(0, 0%, 98%);

  --color-red: hsl(348, 100%, 61%);
  --color-orange: hsl(34, 100%, 50%);

  --color-yellow-inverse: hsl(48, 100%, 77%);
  --color-red-inverse: hsl(348, 100%, 71%);
  --color-orange-inverse: hsl(34, 100%, 70%);

  --color-background: #d6e0e2;
  --color-title: hsl(190, 17.1%, 40%);
  --color-digit: hsl(0, 0%, 45%);
}

/* ==================
 *  Analog Clock
 * ================== */

/* *** CLOCK *** */

.Clocks {
  @apply inline-flex flex-row flex-wrap;
  // @apply spa;
  @apply mx-8 my-5 gap-10;

  .clockWrap {
    @apply w-64 h-96;
    @apply p-5;
    @apply flex flex-col;
    @apply items-start justify-between;
    @apply bg-stone-100;
    @apply border shadow-2xl shadow-stone-200 rounded-2xl;
  }

  /**
   * Clock Meta
   */
  .clockMeta {
    @apply text-base;
    @apply text-gray-500;
  }
  .clockMetaHeadWrap {
    @apply flex flex-row;
    @apply items-center;
  }
  .clockMetaTitle {
    @apply -mt-1 font-semibold text-xl font-sans text-gray-500;
  }

  .analog-clock {
    @apply self-center w-36 h-36;
    @apply border-dotted border-2 border-gray-300;
    border-radius: 50%;
    @apply shadow-xl shadow-stone-200;
  }
  .analog-clock-face {
    height: 100%;
    transform: translateY(50%);
  }
  .analog-clock-face::before {
    content: '';
    width: 8px;
    height: 8px;
    position: absolute;
    @apply bg-stone-100;
    @apply border-2 border-red-400;
    left: 50%;
    z-index: 10;
    transform: translate(-50%, -50%);

    // border: 2px solid var(--color-red-inverse);

    border-radius: 50%;
  }
  
  /* *** DOTS *** */
  
  .dots:nth-of-type(1) {
    transform: rotate(0deg);
  }
  .dots:nth-of-type(2) {
    transform: rotate(90deg);
  }
  .dots:nth-of-type(3) {
    transform: rotate(30deg);
  }
  .dots:nth-of-type(4) {
    transform: rotate(60deg);
  }
  .dots:nth-of-type(5) {
    transform: rotate(120deg);
  }
  .dots:nth-of-type(6) {
    transform: rotate(150deg);
  }
  .dots::before, .dots::after {
    content: '';
    position: absolute;
    @apply bg-slate-300;
    width: 7px;
    height: 2px;
    border-radius: 30px;
    top: -3px;
  }
  .dots::after {
    left: 7px;
  }
  .dots::before {
    right: 7px;
  }
  .dots:nth-of-type(1)::before,
  .dots:nth-of-type(1)::after,
  .dots:nth-of-type(2)::before,
  .dots:nth-of-type(2)::after {
    @apply bg-slate-500;
    width: 10px;
    height: 4px;
  }
  
  /* *** HAND *** */
  
  .hand {
    background: var(--color-black-light);
    transform-origin: 100%;
    border-radius: 10px;
    position: absolute;
    right: 50%;
  }
  .hand.hour-hand {
    width: 25%;
    height: 5px;
    @apply bg-gray-800;
    // @apply bg-transparent border border-black;
    z-index: 1;
  }
  .hand.min-hand {
    width: 37%;
    height: 4px;
  }
  .hand.sec-hand {
    @apply bg-red-400;
    width: 39%;
    height: 2px;
  }
  
  /* ==================
   *  Digital Clock
   * ================== */
  
  .digital-clock {
    @apply relative flex;
    justify-content: center;
    @apply self-center;
    @apply text-2xl text-gray-500 font-mono font-semibold tracking-wide;
  }
  .digit {
    margin: 0 5px;
    @apply w-14 h-14;
    display: flex;
    align-items: center;
    justify-content: center;
    @apply bg-white border;
    @apply rounded-2xl;
    // text-shadow: -1px -1px 0px rgba(255, 255, 255, 0.3), 1px 1px 0px rgba(0, 0, 0, 0.5);
  }
  .digit-sec {
    @apply w-8 h-8;
    @apply text-sm;
    @apply self-start;
    @apply rounded-lg;
    text-shadow: none;
  }
  .meridiem {
    @apply absolute right-2.5 -bottom-0.5;
    @apply text-sm text-gray-400 font-semibold tracking-widest;
  }
}
